$shadowsEnabled: false;
$outlineEnabled: true;

@mixin mobileLandscape() {
    @media (min-width: 480px) {
        @content;
    }
}

@mixin tablet() {
    @media (min-width: 768px) {
        @content;
    }
}

@mixin desktop() {
    @media (min-width: 1024px) {
        @content;
    }
}

@mixin desktop2() {
    @media (min-width: 1280px) {
        @content;
    }
}

@mixin desktop3() {
    @media (min-width: 1440px) {
        @content;
    }
}

@import 'variables';
@import 'mixins';
@import '~@artifexweb/core/styles/functions';
@import '~@artifexweb/core/styles/variables';
@import '~@artifexweb/core/styles/mixins';
@import '~@artifexweb/core/styles/reset';
@import '~@artifexweb/core/styles/components/buttons';
@import '~@artifexweb/core/styles/components/forms';
@import '~@artifexweb/core/styles/components/typography';
@import '~@artifexweb/build/temp/mixins';
@import '~@artifexweb/build/temp/breakpoints';

body {
    background: rebeccapurple;
}

.h2 {
    font-family: Arial, Helvetica, sans-serif;
}
